import img  from './eyeglasses.png';
const img2  = new Image();
img2.src  = img;
// Triangle drawing method
function getRandomColor() {
    var letters = '0123456789ABCDEF';
    var color = '#';
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }
  
 export  const drawPath = (ctx, points, closePath) => {
     if(typeof points[0]  === "object"){
        ctx.beginPath();
         points[0].scaledMesh.map(function(pt,index){
            ctx.strokeStyle = getRandomColor();
            //ctx.moveTo(Math.floor(pt[0]),Math.floor(pt[1]));
            //ctx.lineTo(Math.floor(pt[0]+0.5),Math.floor(pt[1])+0.5); 
            ctx.font = "5px Arial";
            ctx.fillText(index,Math.floor(pt[0]),Math.floor(pt[1]));
            ctx.stroke();
         });
        }
}
  
  